<template>
  <div>
    <el-card style="text-align: left;width: 990px;margin: 35px auto 0 auto">
      <div v-if="accident">
        <el-button style="background-color:gainsboro;width:160px;float:right" @click="downloadJson">
          <span style="font-size: 26px;font-weight: bold">下载</span></el-button>
        <span style="font-size: 30px"><strong>ID:{{accident.id}}详细信息</strong></span>
        <el-divider></el-divider>
        <div><span style="font-size: 20px">测试系统:{{accident.system.type}}</span></div>
        <div><span style="font-size: 20px">事故类型:{{accident.targetAccidents.type}}</span> </div>
        <div><span style="font-size: 20px">事故相关车辆:{{accident.vehicle}}</span> </div>
        <div><span style="font-size: 20px">问题模块:{{accident.faultyModule}}</span> </div>
        <div><span style="font-size: 20px">无人车规划任务:{{accident.taskPlanning.task}}</span></div>
        <div><span style="font-size: 20px">车道类型:{{accident.drivewayType.type}}</span></div>
        <div><span style="font-size: 20px">障碍物类型:{{accident.obstacleType.type}}</span></div>
        <div><span style="font-size: 20px">障碍物数量:{{accident.num}}</span></div>
        <video controls width="900" :src="accident.videoUrl" type="video/mp4"></video>
      </div>
    </el-card>
  </div>
</template>

<script>
  export default {
    name: 'AccidentDetails',
    data () {
      return {
        accident: ''
      }
    },
    mounted () {
      this.loadAccident()
    },
    methods: {
      loadAccident () {
        var _this = this
        this.$axios.get('/accidents/' + this.$route.query.id).then(resp => {
        if (resp && resp.data.code === 200) {
          _this.accident = resp.data.result
          }
        })
      },
      downloadJson () {
        let _this = this
        this.$axios.get('/accidents/download/' + this.accident.id, {
          responseType: 'blob'
        }).then(resp => {
          const blob = new Blob([resp.data], {
            type: 'application/json'
          })
          const link = document.createElement('a')
          link.href = window.URL.createObjectURL(blob) // 指定下载链接
          link.setAttribute('download', _this.accident.fileName) // 指定下载文件名
          document.body.appendChild(link)
          link.click()
          window.URL.revokeObjectURL(link.href) // 释放URL对象
        })
      }
    }
  }
</script>

<style scoped>
  @import "../../styles/markdown.css";
</style>



